:root {
    --primary: #cfccc8;
}

.btn {
    padding: 8px 20px;
    border-radius: 1rem;
    outline: none;
    border: none;
    cursor: pointer;
}

.btn--primary {
    background-color: var(--primary);
    color: #f8f8f7;
    border: 1px solid var(--primary);
}

.btn--outline {
    background-color: transparent;
    color: #cfccc8;
    padding: 8px 20px;
    border: 1px solid var(--primary);
    transition: all 0.3s ease-out;
}

.btn--medium {
    padding: 8px 20px;
    font-size: 20px;
}

.btn--large {
    padding: 12px 26px;
    font-size: 20px;
}

.btn--medium:hover, .btn--large:hover {
    background: #cfccc8;
    color: #f8f8f7;
    transition: all 0.3s ease-out;
}